2, 122, 122);\n  border: 1px solid rgb(192, 192, 192);\n  color: $white;\n  border-radius: 14rem;\n  box-shadow: 0 0 2.1rem 0 rgba(0, 0, 0, 0.3);\n\n  &:focus {\n    outline: none;\n    box-shadow: 0 0 1.4rem 0 rgba(0, 0, 0, 0.452);\n  }\n\n  &::placeholder {\n    color: rgb(185, 185, 185);\n  }\n\n  \n}\n\n.loading-icon {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n\n  &__circle {\n    width: .8rem;\n    height: .8rem;\n    border-radius: 50%;\n    background-color: $white;\n    margin: .55rem;\n    box-shadow: 0 0 1.1rem 0 rgba(0, 0, 0, 0.281);\n    animation-name: circleJump;\n    animation-duration: 1.4s;\n    animation-iteration-count: infinite;\n    animation-timing-function: cubic-bezier(.7,.09,.43,.98);\n\n    &--1 {\n      animation-delay: 200ms;\n    }\n\n    &--2 {\n      animation-delay: 350ms;\n    }\n\n    &--3 {\n      animation-delay: 500ms;\n    }\n  }\n}\n\n@keyframes circleJump {\n\n  30% {\n    transform: translateY(-.55rem);\n  }\n\n  60% {\n    transform: translateY(0);\n  }\n}\n\n@import './popup';\n\n@import './variables';\n\n@import './markerPopup';\n\n\n\n\n@media (max-width: 1300px) {\n  .title-box h1 {\n    font-size: 3.5rem;\n  }\n}\n\n@media (max-height: 860px) {\n  .popup {\n    transform: translate(-50%, 0) scale(0.8);\n    top: 6rem;\n  }\n}\n\n@media (max-height: 740px) {\n  .popup {\n    transform: translate(-50%, 0) scale(0.7);\n    top: 3rem;\n  }\n}\n\n@media (max-height: 650px) {\n  .popup {\n    transform: translate(-50%, 0) scale(0.6);\n    top: 1rem;\n  }\n}\n\n\n@media (max-width: 700px) {\n  .title-box h1 {\n    font-size: 2.8rem;\n  }\n\n  .search-box {\n    font-size: 1.4rem;\n    width: 30rem;\n  }\n\n  .popup {\n    width: 90%;\n    &__controls {\n      margin-top: 1rem;\n    }\n    &__data {\n      transform: translateY(-12rem);\n      &-temp {\n        font-size: 8rem;\n\n        &--cur {\n          &-dec {\n            font-size: 2rem;\n\n            &::after {\n              font-size: 2rem;\n            }\n          }\n        }\n      }\n\n      &-maxmin {\n        font-size: 2rem;\n\n        &-max, &-min {\n          &::after {\n            vertical-align: 80%;\n          }\n        }\n      }\n\n      &-weather {\n        &--icon {\n          .bi {\n            width: 6rem;\n            height: 6rem;\n          }\n        }\n      }\n    }\n  }\n}\n\n\n",".popup {\n  z-index: 20;\n  position: absolute;\n  top: 12rem;\n  left: 50%;\n  transform: translate(-50%, 0);\n  background-color: rgba($light-purple, .7);\n  box-shadow: 1px 2px 2.8rem 0 rgba(0, 0, 0, 0.418);\n  border-radius: 5.5rem;\n  width: 80%;\n  max-width: 150rem;\n  height: 75%;\n  min-height: 72rem;\n  opacity: 0;\n  visibility: hidden;\n  transition: visibility 0s, opacity 1s ease;\n  display: grid;\n  display: flex;\n  flex-direction: column-reverse;\n  justify-content: space-between;\n  align-items: center;\n\n  &__controls {\n    height: 9rem;\n    width: 100%;\n    display: flex;\n    align-items: center;\n    justify-content: space-between;\n    margin-top: 2rem;\n\n    .bi {\n      color: $white;\n      filter: drop-shadow(0 0 4px rgba(0, 0, 0, 0.301));\n    }\n\n    > div {\n      cursor: pointer;\n      transition: transform 300ms ease;\n      margin: 0 4rem;\n\n      &:hover {\n        filter: drop-shadow(2px 2px 1.1rem rgba(0, 0, 0, 0.178));\n        transform: scale(1.2);\n      }\n    }\n\n    &-close-btn {\n      width: 2rem;\n      height: 2rem;\n    }\n\n    &-bookmark-btn .bi {\n      width: 4.1rem;\n      height: 4.1rem;\n    }\n  }\n\n  &__loading {\n    width: 100%;\n    height: 100%;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n\n  }\n\n  &__error {\n    height: 100%;\n    width: 100%;\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    align-items: center;\n\n    &-msg {\n      font-size: 4rem;\n      color: $white;\n      text-align: center;\n      text-shadow: 1px 1px .6rem rgba(0, 0, 0, 0.315);\n      margin-bottom: 10rem;\n    }\n\n    &-retry-btn {\n      border: none;\n      border-radius: 1rem;\n      font-size: 3rem;\n      text-shadow: 1px 1px .6rem rgba(0, 0, 0, 0.315);\n      color: $white;\n      background-color: rgba(darken($light-purple, 20%), 0.3);\n      padding: 1rem;\n      cursor: pointer;\n      transition: transform 250ms ease;\n\n      &:hover {\n        transform: scale(1.2);\n      }\n    }\n  }\n\n  // &__map {\n  //   grid: map;\n  //   grid-row: 1/3;\n  //   background-color: rgba(darken($light-purple, 20%), 0.3);\n  //   margin: 1.5rem;\n  //   border-radius: 4rem;\n  // }\n\n  &__data {\n    transform: translateY(-6rem);\n    width: 100%;\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    color: $white;\n    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.568);\n\n    &-temp {\n      font-size: 12rem;\n      font-weight: 200;\n      display: flex;\n      flex-direction: row;\n      align-items: baseline;\n      transform: translateX(1rem);\n      \n\n      &--cur {\n        &-dec {\n          font-size: 3rem;\n          font-weight: 300;\n\n          &::after {\n            content: \"°C\";\n            vertical-align: 180%;\n            font-size: 2.8rem;\n            font-weight: 300;\n            color: $white;\n          }\n        }\n      }\n    }\n\n    &-maxmin {\n      font-size: 3rem;\n      font-weight: 300;\n      display: flex;\n      flex-direction: row;\n      align-items: center;\n\n      &-max, &-min {\n        &::after {\n          content: \"°C\";\n          font-size: 1rem;\n          font-weight: 400;\n          vertical-align: 120%;\n        }\n      }\n\n      &-max {\n        transform: translateX(-.5rem);\n        color: $high-temp-color;\n      }\n\n      &-min {\n        color: $low-temp-color;\n      }\n\n      &-bar {\n        border-radius: 40%;\n        margin: 0 1rem;\n        padding: .8rem 0;\n        border: 2px solid $white;\n      }\n    }\n\n    &-location {\n      font-size: 2rem;\n      margin: 1rem;\n      text-align: center;\n      text-transform: capitalize;\n    }\n\n    &-date {\n      font-size: 1.6rem;\n    }\n\n    &-time {\n      font-size: 2.5rem;\n      margin: .4rem;\n    }\n\n    &-weather {\n      display: flex;\n      flex-direction: row;\n      align-items: center;\n      justify-content: center;\n\n      &--icon {\n        margin-right: 2rem;\n        filter: drop-shadow(0 0 .8rem rgba(0, 0, 0, 0.315));\n\n        .bi {\n          width: 12rem;\n          height: 12rem;\n        }\n      }\n\n      &--desc {\n        font-size: 2rem;\n        text-transform: capitalize;\n      }\n    }\n\n    &-forecast {\n      display: flex;\n      flex-direction: row;\n      align-items: center;\n      width: 80%;\n      max-width: 80rem;\n      height: 22rem;\n      overflow: scroll;\n      overflow-y: hidden;\n\n      &-item {\n        margin: .6rem;\n        min-width: 14rem;\n        height: 18rem;\n        border-radius: 1rem;\n        background-color: rgba(darken($light-purple, 20%), 0.3);\n        box-shadow: 2px 2px .6rem rgba(0, 0, 0, 0.322);\n\n        .bi {\n          margin: 1.4rem;\n          width: 4rem;\n          height: 4rem;\n        }\n        \n        &--weather {\n          display: flex;\n          flex-direction: row;\n          align-items: center;\n          justify-content: space-between;\n          \n          &-temp {\n            font-size: 3rem;\n            font-weight: 300;\n            margin-right: 1rem;\n\n            &::after {\n              content: \"°C\";\n              margin-left: 1px;\n              font-size: 1.2rem;\n              font-weight: 400;\n              vertical-align: 100%;\n            }\n          }\n        }\n\n        &--maxmin {\n          display: flex;\n          flex-direction: row;\n          align-items: center;\n          justify-content: center;\n          margin-bottom: .6rem;\n\n          &-max, &-min {\n            font-size: 1.8rem;\n            font-weight: 400;\n\n            &::after {\n              content: \"°C\";\n              font-size: 1rem;\n              font-weight: 400;\n              vertical-align: 50%;\n            }\n          }\n\n          &-bar {\n            border: 1px solid $white;\n            border-radius: 40%;\n            margin: 0 1.4rem;\n            width: 0;\n            padding: .6rem 0;\n          }\n\n          &-max {\n            color: $high-temp-color;\n          }\n\n          &-min {\n            color: $low-temp-color;\n          }\n        }\n\n        &--bar {\n          border: 2px solid $white;\n          border-radius: 40%;\n          height: 0;\n          width: 80%;\n          margin: 0 auto;\n          box-shadow: 0 2px 1rem rgba(0, 0, 0, 0.4);\n        }\n\n        &--date {\n          margin-top: .4rem;\n          text-align: center;\n          font-size: 1.4rem;\n        }\n\n        &--desc {\n          margin-top: .4rem;\n          font-size: 1.7rem;\n          text-transform: capitalize;\n          text-align: center;\n        }\n      }\n    }\n  }\n}\n\n","@import url('https://fonts.googleapis.com/css2?family=Lato:wght@100;300;400;700;900&display=swap');\n\n.leaflet-popup-content-wrapper {\n  background-color: transparent;\n  box-shadow: 0 0 0 0 transparent;\n  width: 12rem;\n  height: 22rem;\n  border: 2px solid $white;\n  box-shadow: 2px 2px 1rem 0 rgba(0, 0, 0, 0.541);\n  background-color: rgba($light-purple, .9);\n}\n\n.leaflet-popup-content-wrapper .leaflet-popup-content {\n  color: $white;\n  height: 20rem;\n  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.568);\n}\n\n.leaflet-popup-tip {\n  background-color: $white;\n}\n\n.marker-popup {\n  height: 100%;\n  display: flex;\n  flex-direction: column;\n  \n  &__cur-weather {\n    z-index: 5;\n    height: 4rem;\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    font-size: 2rem;\n    font-family: 'Lato', sans-serif;\n    font-weight: 300;\n    \n    .bi {\n      width: 3rem;\n      height: 3rem;\n    }\n    \n    &--temp {\n      &::after {\n        margin-left: 2px;\n        content: \"°C\";\n        vertical-align: 60%;\n        font-size: 1rem;\n        font-weight: 400;\n        color: $white;\n      }\n    }\n  }\n\n  &__maxmin {\n    font-size: 1.4rem;\n    font-weight: 300;\n    display: flex;\n    height: 3.5rem;\n    justify-content: center;\n    align-items: center;\n\n    &--max, &--min {\n      &::after {\n        content: \"°C\";\n        vertical-align: 40%;\n        font-size: 1rem;\n        font-weight: 400;\n      }\n    }\n\n    &--max {\n      color: $high-temp-color;\n    }\n\n    &--min {\n      color: $low-temp-color;\n    }\n\n    &--bar {\n      border: 1px solid $white;\n      height: 3rem;\n      margin: 0 .8rem;\n      border-radius: 40%;\n      width: 0;\n    }\n  }\n\n  &__bar {\n    border: 2px solid $white;\n    height: 0;\n    width: 100%;\n    margin: .5rem 0;\n    border-radius: 40%;\n  }\n\n  &__date {\n    font-size: 1.4rem;\n    transform: translateY(-1.4rem);\n    text-align: center;\n  }\n\n  &__time {\n    font-size: 1.6rem;\n    text-align: center;\n    transform: translateY(-5rem);\n  }\n\n  &__remove {\n    border: none;\n    background-color: transparent;\n    color: darken($high-temp-color, 40%);\n    transform: translateY(-6.5rem);\n    padding: .5rem;\n    cursor: pointer;\n    border-radius: 1rem;\n    transition: color 200ms ease, background-color 200ms ease;\n\n    .bi {\n      width: 3rem;\n      height: 3rem;\n      transform: translateY(.1rem);\n    }\n\n    &:hover {\n      color: darken($high-temp-color, 20%);\n      background-color: rgba(0,0,0, 0.6);\n    }\n  }\n\n  &__expand {\n    z-index: 10;\n    transform: translate(-1.8rem, -27.8rem);\n    cursor: pointer;\n    background-color: transparent;\n    width: 2.2rem;\n    height: 2.2rem;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    transition: transform 200ms ease;\n    filter: drop-shadow(0 0 2px black);\n    \n    .bi {\n      width: 1.8rem;\n      height: 1.8rem;\n      color: $white;\n      transition: transform 200ms ease;\n    }\n\n    &:hover {\n      transform: translate(-1.8rem, -27.8rem) scale(3);\n\n      .bi {\n        color: darken($white, 5%);\n      }\n    }\n  }\n}","$light-purple: rgb(128, 128, 219);\n// $light-purple: rgb(102, 163, 110);\n// $light-purple: rgb(104, 113, 240);\n$white: rgb(236, 236, 236);\n\n$high-temp-color: rgb(235, 137, 137);\n\n$low-temp-color: rgb(127, 209, 241);"],"sourceRoot":""}]);
